	module.exports = {"data":{"markdownRemark":{"html":"<p><img src=\"/images/2015/07/reacteurope-1.png\"></p>\n<p>Last week I was lucky enough to attend the ReactEurope Event in Paris, and it was a really interesting experience. Speakers and talks were amazing, and the organization of the event was perfect.</p>\n<p>Here are some notes I take during the event(Raw):</p>\n<p><strong>Inline Style:</strong> Inline style seems to be a good practice for state related style. And javascript style library like <a href=\"https://github.com/FormidableLabs/radium\">Radium</a> are really useful for inline style.</p>\n<p><strong>GraphQL:</strong> GraphQL seems to be a really good solution to serve data to multiple clients, and allow you to limit the number of request and data to fetch for mobile devices. It’s a promising new approach to retrieve Data, and it could be plugged over your existing datastore (API REST, ...). A <a href=\"https://github.com/facebook/graphql\">Specification Draft</a> of graphQL and an implementation of a <a href=\"https://github.com/graphql/graphql-js\">graphQL Server</a> were released during the event.</p>\n<p><strong>Relay</strong>: the glue between graphQL server and your's React Application should be released in august 2015.</p>\n<p><strong>Animation:</strong> RIP ReactCSSTransitionGroup, the future of animation in react and react-native should bee less painfull.</p>\n<p><strong>Redux and hot reload</strong>: <a href=\"https://github.com/gaearon/redux\">Redux flux implemenation</a>, with store as reductor function, seems to be the Flux library to use in 2015. Hot reload improve really the developper experience and the future redux dev tools with state history navigation demo was amazing and should be released soon.</p>\n<p>With a huge comunity, a great developper experience, reactJS and flux is really a great solution to build amazing UI. Add to this react-native for mobile development, and react should be the library to use in 2015.</p>","frontmatter":{"date":"2015-07-06","path":"/reacteurope-2015-my-notes","title":"ReactEurope 2015: Notes"}}},"pathContext":{}}

